import { Card } from '@mui/material';
import BudgetTable from './ProposalTable';
import { api } from 'src/api/api';
import React from 'react';
import { IBudget } from 'src/models/product';
import ProposalTable from './ProposalTable';

function Proposal() {

  const [proposal, setProposal] = React.useState([]);
  
  async function getAllproposal(){
    const {data} = await api.get("/propostas");
    setProposal(data);
  }

  React.useEffect(()=>{
    getAllproposal();
  },[])

  

  return (
    <Card>
        <ProposalTable proposal={proposal?proposal:[]}  className=''/>
    </Card>
  );
}

export default Proposal;
