import { Typography, Button, Grid } from '@mui/material';
import AddTwoToneIcon from '@mui/icons-material/AddTwoTone';
import React from 'react';
import { useContext } from 'react';
import { UserContext } from 'src/context/UserContext';
import { BudgetsModal } from 'src/components/Modals/BudgetsModal/BudgetsModal';
import { ProposalModal } from 'src/components/Modals/ProposalModal/ProposalModal';

function PageHeader() {
  const [modal, setModal] = React.useState<Boolean>(false);
  const [modal2, setModal2] = React.useState<Boolean>(false);
  const {
    usuario
  }= useContext(UserContext);
  return (
    <Grid container justifyContent="space-between" alignItems="center">
      <Grid item>
        <Typography variant="h3" component="h3" gutterBottom>
          Orçamentos | Propostas
        </Typography>
        <Typography variant="subtitle2">
          Gere um novo orçamento | proposta para seus clientes.
        </Typography>
      </Grid>
      <Grid item>
      <Button
          sx={{ mt: { xs: 2, md: 0 } }}
          variant="contained"
          startIcon={<AddTwoToneIcon fontSize="small" />}
          style={{backgroundColor: "rgb(18, 80, 130)", marginRight: "1rem"}}
          onClick={(e)=>setModal(!modal)
          }
        >
          Gerar orçamento
        </Button>
        <Button
          sx={{ mt: { xs: 2, md: 0 } }}
          variant="contained"
          startIcon={<AddTwoToneIcon fontSize="small" />}
          style={{backgroundColor: "rgb(18, 80, 130)"}}
          onClick={(e)=>setModal2(!modal2)
          }
        >
          Gerar proposta
        </Button>
      </Grid>
      {
        modal && (
          <BudgetsModal data={null} setModal={setModal} openModal={modal} edit={false}/>
        )
      }

      {
        modal2 &&(
          <ProposalModal data={null} setModal2={setModal2} openModal2={modal2} edit={false}/>
        )
      }
      
    </Grid>
  );
}

export default PageHeader;
