import { Card } from '@mui/material';
import BudgetTable from './BudgetTable';
import { api } from 'src/api/api';
import React from 'react';
import { IBudget } from 'src/models/product';
import Tabs from './Tabs/Tabs';
import ProposalTable from '../Proposal/ProposalTable';
import Proposal from '../Proposal/Proposal';

function Orders() {

  const [budgets, setBudgets] = React.useState<IBudget[]>([]);
  
  async function getAllBudgets(){
    const {data} = await api.get("/orcamentos");
    setBudgets(data);
  }
  

  React.useEffect(()=>{
    getAllBudgets();
  },[])

  const tabs = [
    {
      title: 'Orçamentos',
      content: <BudgetTable budgets={budgets?budgets:[]}  className=''/>,
    },
    {
      title: 'Propostas',
      content: <Proposal/>,
    }
  ];


  return (
    <Card>
      <Tabs tabs={tabs}/>
    </Card>
  );
}

export default Orders;
