import { FC, ChangeEvent, useState, useCallback } from 'react';
import {utils, writeFileXLSX } from 'xlsx';
import PropTypes from 'prop-types';
import { api } from 'src/api/api';
import {
  Tooltip,
  Divider,
  Box,
  Card,
  Checkbox,
  IconButton,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TablePagination,
  TableRow,
  TableContainer,
  Typography,
  useTheme,
  CardHeader
} from '@mui/material';

import {FaCloudDownloadAlt} from 'react-icons/fa'
import { IBudget } from 'src/models/product';
import {BsEye} from 'react-icons/bs'
import { BudgetsModal } from 'src/components/Modals/BudgetsModal/BudgetsModal';
import moment from 'moment';
import Tabs from './Tabs/Tabs';

interface BudgetTableProps {
  className: string;
  budgets: IBudget[];
}


const applyFilters = (
  budgets: IBudget[],
): IBudget[] => {
  return budgets.filter((budget) => {
    let matches = true;

    /*if (filters.status && cryptoOrder.status !== filters.status) {
      matches = false;
    }*/

    return matches;
  });
};

const applyPagination = (
  budgets: IBudget[],
  page: number,
  limit: number
): IBudget[] => {
  return budgets.slice(page * limit, page * limit + limit);
};

const BudgetTable: FC<BudgetTableProps> = ({ budgets }) => {
  const [selectedBudgets, setSelectedBudgets] = useState<string[]>([]);
  const selectedBulkActions = selectedBudgets.length > 0;
  const [page, setPage] = useState<number>(0);
  const [limit, setLimit] = useState<number>(5);


  const exportFile = useCallback(() => {
    const ws = utils.json_to_sheet(budgets);
    const wb = utils.book_new();
    utils.book_append_sheet(wb, ws, "Data");
    writeFileXLSX(wb, "Orcamentos.xlsx");
  }, [budgets]);

  const handleSelectAllBudgets= (event: ChangeEvent<HTMLInputElement>, ): void => {
    setSelectedBudgets(
      event.target.checked
        ? budgets.map((budget) => budget.id_tokencontrato)
        : []
    );
  };

  const handleSelectOneBudget = (event: ChangeEvent<HTMLInputElement>, budget: string): void => {
    if(!selectedBudgets.includes(budget)) {
      setSelectedBudgets((prevSelected) => [
        ...prevSelected,
        budget
      ]);
    }else {
      setSelectedBudgets((prevSelected) =>
        prevSelected.filter((id) => id !== budget)
      );
    }
  };

  const handlePageChange = (event: any, newPage: number): void => {
    setPage(newPage);
  };

  const handleLimitChange = (event: ChangeEvent<HTMLInputElement>): void => {
    setLimit(parseInt(event.target.value));
  };

  const filteredBudgets = applyFilters(budgets);

  const paginatedBudgets = applyPagination(
    filteredBudgets,
    page,
    limit
  );
  const selectedSomeBudgets =
    selectedBudgets.length > 0 &&
    selectedBudgets.length < budgets.length;
  const selectedAllBudgets =
  selectedBudgets.length === budgets.length;

  const theme = useTheme();

  const [modal, setModal] = useState<Boolean>(false)
  const [data, setData] = useState([]);



  const [linkContrato, setLinkContrato] = useState('');

  async function getStatusDocument(linkContrato: string){
    const fetchDocument = fetch(`https://nestrental.com.br:80/api/clicksign/get-document/${linkContrato}`, {
    method: 'POST',
    headers:{
        'Content-Type': 'application/json'
    },
    })

    const response = await fetchDocument;
    const json = await response.json();
    setLinkContrato(json?.data.document.downloads?.original_file_url);
    window.open(json?.data.document.downloads?.original_file_url)

  }

  const tabs = [
    {
      title: 'Orçamentos',
      content: 'Conteúdo da Tab 1',
    },
    {
      title: 'Propostas',
      content: 'Conteúdo da Tab 2',
    }
  ];

 
  

  return (
    <Card>
      {!selectedBulkActions && (
        <CardHeader
          action={
            <Box width={150}>
               <FaCloudDownloadAlt onClick={exportFile} style={{
                    textAlign: 'right',
                    position: 'relative',
                    left: '7rem',
                    height: '25px',
                    width: '25px',
                    cursor: 'pointer'
                }}/>
            </Box>
          }
          title="Orçamentos cadastrados"
        />
      )}
      <Divider />
      <TableContainer>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell >
                Empresa
              </TableCell>
              <TableCell>Início</TableCell>
              <TableCell>Devolução</TableCell>
              <TableCell align='center'>Valor</TableCell>
              <TableCell align="right">Ações</TableCell>
              {/*<TableCell align="right">Status</TableCell>*/}
             
            </TableRow>
          </TableHead>
          <TableBody>
            {paginatedBudgets.map((budget) => {
              const isBudgetSelected = selectedBudgets.includes(
                budget?.id_tokencontrato
              );
              return (
                <TableRow
                  hover
                  key={budget?.id_tokencontrato}
                  selected={isBudgetSelected}
                >
                  
                  <TableCell>
                    <Typography
                      variant="body1"
                      fontWeight="bold"
                      color="text.primary"
                      gutterBottom
                      noWrap
                    >
                      {budget.empresa}
                    </Typography>
                  </TableCell>
                  <TableCell>
                    <Typography
                      variant="body1"
                      fontWeight="bold"
                      color="text.primary"
                      gutterBottom
                      noWrap
                    >
                      {moment(budget?.data_inicio).utc().format('DD/MM/yyyy')}
                    </Typography>
                  </TableCell>

                  <TableCell>
                    <Typography 
                        variant="body1"
                        fontWeight="bold"
                        color="text.secondary"
                        gutterBottom 
                        noWrap
                    >
                      {moment(budget?.data_devolucao).utc().format('DD/MM/yyyy')}
                    </Typography>
                  </TableCell>

                  <TableCell align="right">
                    <Typography
                      variant="body1"
                      fontWeight="bold"
                      color="text.primary"
                      gutterBottom
                      noWrap
                    >
                    </Typography>
                    <Typography variant="body2" color="text.secondary" noWrap>
                      {(budget?.valor).toLocaleString('pt-br', { style: 'currency', currency: 'BRL' })}
                    </Typography>
                  </TableCell>
                  <TableCell align="right">
                    <Tooltip title="Visualizar orçamento" arrow>
                      <IconButton
                        sx={{
                          '&:hover': {
                            background: theme.colors.primary.lighter
                          },
                          color: theme.palette.primary.main
                        }}
                        color="inherit"
                        size="small"
                        onClick={(e)=>getStatusDocument(budget?.id_tokencontrato)}
                      >
                        <BsEye style={{width: '20px', height: '20px'}}/>
                        
                      </IconButton>
                    </Tooltip>
                    {/*<Tooltip title="Excluir pedido" arrow>
                      <IconButton
                        sx={{
                          '&:hover': { background: theme.colors.error.lighter },
                          color: theme.palette.error.main
                        }}
                        color="inherit"
                        size="small"
                      >
                        <DeleteTwoToneIcon fontSize="small" />
                      </IconButton>
                      </Tooltip>*/}
                  </TableCell>
                </TableRow>
              );
            })}
          </TableBody>
        </Table>
      </TableContainer>
      <Box p={2}>
        <TablePagination
          component="div"
          count={filteredBudgets.length}
          onPageChange={handlePageChange}
          onRowsPerPageChange={handleLimitChange}
          page={page}
          rowsPerPage={limit}
          rowsPerPageOptions={[5, 10, 25, 30]}
        />
      </Box>
      
      {data && modal &&(
        <BudgetsModal 
          openModal={modal}
          setModal={setModal}
          data={data}
          edit={true}
      />)}
      
    </Card>
  );
};

BudgetTable.propTypes = {
  budgets: PropTypes.array.isRequired
};

BudgetTable.defaultProps = {
  budgets: []
};

export default BudgetTable;
