    //criar Proposta
    async function createProposal(e:  React.FormEvent<HTMLFormElement>){
        e.preventDefault();
        setLoading(true)
        let opt = null;
        if(selectedCheckbox === 'checkbox1'){
            opt = 3
        }else if(selectedCheckbox === 'checkbox2'){
            opt = 13
        }else{
            opt = 14
        }
        const fetchApi = fetch(`https://nestrental.com.br:80/api/propostas`, {
            headers:{
                'Content-Type': 'application/json',
            }, 
            method: 'POST',
            body: JSON.stringify({
                "id_tokencontrato": '0',
                "data_inicio": startDate,
                "data_devolucao": endDate,
                "empresa": nomeFantasiaNewCLient ? nomeFantasiaNewCLient: clientData.entidade.nome_fantasia,
                "valor": billingValue + newPrice,
                "solicitante": nomeSolicitante,
                "email_fatura": emailFatura,
                "id_opcao_pagamento": opt
            
        })
    }).then(async(res)=>{
        setMessage('');
        setErrorData('');
        if(res.status == 202){
            if(verifyClient){
                const createdZoho = await createAccountZohoNewClient();
                if(createdZoho){
                    setMessage('Proposta criada com sucesso.');
                    createDocumentPdf();
                   
                }else{
                    setErrorData('Este CNPJ já está vinculado a uma conta. Tente novamente!');
                }
            }else{
                createDocumentPdf();
             
                setMessage('Proposta criada com sucesso.');

    
            }
        }
    }).catch((error)=>{
        console.error(`${error} -  Erro na criação da proposta`)
    })
    setLoading(false);
}



async function buscar_cidade(nome_cidade: string) {
    //Considerando apenas cidades de sao paulo.
    const nome = encodeURI(nome_cidade);
    const { data } = await api.get(`/cidades/${nome}`);
    return data?.id;
}


