async function criar_endereco_entrega(id_entidade) {
    const { data } = await api.post(`/enderecos/${id_entidade}`, {
        "id_cidade": await buscar_cidade(shippingCityNewClient),
        "cep": cepENewClient,
        "bairro": shippingNeighbourhoodNewClient,
        "complemento": shippingComplementNewClient? shippingComplementNewClient: 'Não possui',
        "rua": shippingStreetNewClient,
        "numero": shippingNumberNewClient,
        "contato": nameResp,
        "telefone": tel_company.value,
        "email": emailNewClient,
        "nome_obra": nameResp,
        tipo: "E" //E = Entrega C = Cobrança
    });
    return data;
}


    
